import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'translate'
})
export class TranslatePipe implements PipeTransform {

  languageSet = {
    tamil: {
      'Home': 'வீடு',
      'Login': 'உள்நுழைய',
      'Signup': 'பதிவுபெறுதல்',
      'Errorpage': 'd',
      'Dummy': 'போலி',
      'Pagination':'பக்க வரிசைப்பாடு',
      'Employee List': 'ஊழியர் பட்டியல்',
      'Employee Detail':'பணியாளர் விவரம்',
      'Pipes':'குழாய்கள்',
      'Bar chart':'பார்சார்ட்'
    },
    english: {
      'Home': 'Home',
      'Login': 'Login',
      'Signup': 'Signup',
      'Errorpage': 'Errorpage',
      'Dummy': 'Dummy',
      'Pagination': 'Pagination',
      'Employee List': 'Employee List',
      'Employee Detail':'Employee Detail',
      'Pipes':'Pipes',
      'Bar chart':'Bar chart'
    },
    hindi: {
      'Home': 'घर',
      'Login': 'लॉग इन करें',
      'Signup': 'साइन अप करें',
      'Errorpage': 'd',
      'Dummy': 'डमी',
      'Pagination': 'पृष्ठ संरेखण',
      'Employee List': 'कर्मचारी सूची',
      'Employee Detail':'कर्मचारी विस्तार',
      'Pipes':'पाइप',
      'Bar chart':'बार चार्ट'
    }
  }

  transform(value: any,args: string): string {
    console.log("Value : " + value);
    console.log("args : " + args);
    let language = 'tamil';
    if (localStorage.getItem('language')) {
      language = localStorage.getItem('language');
    }
    if(args){
      language=args;
    }
    if (this.languageSet[language][value]) {
      return this.languageSet[language][value];
    }
    return 'hii';
  }



}
